@import 'reset.scss';
@import 'smart-grid.scss';
@import './hamburger/hamburger.scss';

@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Gilroy/Gilroy-Regular.ttf);
}

@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/Gilroy/Gilroy-Medium.ttf);
}

@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../fonts/Gilroy/Gilroy-Bold.ttf);
}

a {
  text-decoration: none;
  color: #000;
}

img {
  max-width: 100%;
}

html {
  font-family: "Gilroy";
  font-size: 11.7px;
  font-weight: 400;
}

$background: #F2F2F2;
$elem: #FEFEFE;
$nav_link: #5B5B5B;
$font_black: #333333;
$links_blue: #2B2E83;

.wrapper {
  @include wrapper();
}

.header-content {
  background-color: #fff;
  @include row-flex();
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;

  @include md(padding-top, 8px);
  @include md(padding-bottom, 8px);

  @media (max-width: 1210px) {
    justify-content: space-between;
  }


  &__logo {
    @include col();
    margin-right: 40px;
    max-width: 237px;
    @include md(margin-bottom, 0px);
    @include md(max-width, 220px);
    @include sm(max-width, 180px);
  }

  &__nav {
    @include col();
    flex-grow: 1;
    display: flex;
    align-items: center;
    @media (max-width: 1210px) {
      display: none;
    }
    

    ul {
      display: flex;
      flex-grow: 1;

      li {
        margin-right: 45px;

        @include lg(margin-right, 40px);
        

        &:last-child{
          margin-right: 0px;
        }

        a {
          font-size: 1rem;
          color: $nav_link;

          &:after {
            position: absolute;
            bottom: -5px;
            display: block;
            left: 50%;
            content: '';
            height: 1px;
            width: 0;
            background-color: $links_blue;
          } 
        }

        &:hover {

          a {
            position: relative;
            color: $links_blue;

            

            &:after {
              width: 100%;
              transform: translateX(-50%);
              transition: width .2s ease-out;
            }
          }
        }
      }
    }

    &-contact {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-right: 40px;

      &:last-child {
        margin-right: 0;
      }

      p {
        font-size: 1.125rem;
        color: $font_black;
        margin-bottom: 5px;
      }

      a {
        font-size: 0.75rem;
        color: $links_blue
      }
    }

    &--open {
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      opacity: 1;
      position: absolute;
      top: 60px;
      right: 0;
      width: 100%;
      height: 110%;
      background-color: #fff;
      z-index: 1;

      @include sm(top, 50px);
      @include sm(height, 120%);

      ul {
        padding-top: 80px;
        padding-bottom: 40px;
        width: 80%;
        border-bottom: 2px solid #F2F2F2;
        
        @include sm(padding-top, 40px);

        li {
          padding-bottom: 15px;

          @include sm(padding-bottom, 35px);

          &:hover {

            a {
              color: #2B2E83;
            }
          }

          &:last-child {
            padding-bottom: 0px;
          }

          a {
            font-size: 1.7rem;
            color: #5B5B5B;
          }
        }
      }

      &-contact {
        padding-top: 40px;

        &:last-child {
          padding-top: 20px;
        }

        p {
          font-size: 1.5rem;
          margin-bottom: 6px;
        }

        a {
          color: #2B2E83;
          font-size: 1rem;
        }
      }
    }
  }
}


.company {
  background-image: url('../assets/img/background_home.png');
  background-repeat: repeat;
  background-position: top;
  background-size: 1690px;
  height: 420px;
  max-width: 1920px;
  margin: 0 auto;

  @include lg(height, 430px);
  @include md(height, 410px);
  @include sm(height, 390px);

  &-content {

    &__text {
      width: 100%;
      text-align: center;
      padding-top: 125px;
      color: #FFEEFF;

      @include sm(padding-top, 35px);

      h1 {
        font-size: 3rem;
        font-weight: 700;
        padding-bottom: 25px;

        @include lg(font-size, 2.5rem);
        @include lg(line-height, 2.5rem);
        @include md(font-size, 2.2rem);
        @include md(line-height, 2.2rem);
        @include sm(font-size, 2rem);
        @include sm(max-width, 320px);
        @include sm(margin, 0 auto);
      }

      h2 {
        font-size: 1.75rem;
        line-height: 2.5rem;
        font-weight: 500;
        max-width: 660px;
        margin: 0 auto;

        @include lg(font-size, 1.5rem);
        @include lg(line-height, 2rem);
        @include md(font-size, 1rem);
        @include md(line-height, 1.4rem);
        @include sm(line-height, 1.7rem);
        @include sm(font-size, 1.3rem);
        @include sm(max-width, 260px);
      }
    }
  }
}


.cards {

  &-content {
    height: 320px;
    @include col();

    @include sm(height, 440px);
    @media (max-width: 451px) {
      height: 400px;
    }
    @media (max-width: 360px) {
      height: 430px;
    }

    &__cards {
      margin-top: -125px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      @include sm(justify-content, center);
      @include sm(margin-top, -160px);
      @media (max-width: 360px) {
        margin-top: -180px;
      }

      .card {
        margin: 6px;
        @media (max-width: 451px) {
          margin-left: 50px;
          margin-right: 50px;
        }
        @media (max-width: 420px) {
          margin-left: 40px;
          margin-right: 40px;
        }
        @media (max-width: 340px) {
          margin-left: 10px;
          margin-right: 10px;
        }
        background-color: $elem;
        border-radius: 4px;
        box-shadow: 0px 3px 7px rgba(51, 51, 51, 0.15);


        &__text {
          padding: 25px 25px 0px 25px;

          @include sm(padding, 30px 30px 0px 30px);
          
          h3 {
            font-size: 1.75rem;
            font-weight: 700;
            color: #333333;
            margin-bottom: 8px;

            @include sm(font-size, 2rem);
            @include sm(margin-bottom, 12px);
          }

          p {
            font-size: 1rem;
            color: #828282;
            line-height: 1.5rem;

            @include sm(font-size, 1.2rem);
            @include sm(line-height, 1.9rem);
            @include sm(padding-bottom, 15px);
          }
        }

        img {
          display: block;
          margin-left: auto;
          max-height: 107px;

          @include sm(max-height, 150px);
        }
      }
    }
  }
}


footer {
  background-color: #202020;

  .footer-content {
    display: flex;
    width: 100%;
    justify-content: center;
    font-size: 1rem;
    padding-top: 30px;
    padding-bottom: 30px;

    @include sm(flex-direction, column);
    @include sm(align-items, center);
    
    p {
      color: $elem;
      margin-right: 10px;

      @include sm(margin-right, 0);
      @include sm(padding-bottom, 20px);
    }

    a {
      color: $elem;
      margin-right: 10px;

      @include sm(margin-right, 0);
      @include sm(padding-bottom, 20px);

      &:last-child {
        margin-right: 0;
        @include sm(padding-bottom, 0px);
      }
    }
  }
}


.alice-carousel__dots-item:hover, .alice-carousel__dots-item.__active {
  display: none;
  @media (max-width: 1250px) {
    display: inline-block;
  }
}

.hamburger {
  padding: 0px;
  display: none;
  @include col();

  @media (max-width: 1210px) {
    display: inline-block;
  }

  &-box {
    width: 30px;
    height: 16px;
  }

  &--spring &-inner::before {
    top: 8px;
  }

  &--spring &-inner::after {
    top: 16px;
  }

  &-inner, &-inner::before, &-inner::after {
    width: 30px;
    height: 2px;
    background-color: #2B2E83;
  }

  &.is-active &-inner, &.is-active &-inner::before, &.is-active &-inner::after {
    background-color: #2B2E83;
  }
}

